import React from 'react'
import './Product.css';

function Product() {
    return (
        <div className='product'>
            <div className='product_info'>
                <p>OnePlus 8 Pro</p>
                <p className='product_price'>
                    <small>$</small>
                    <strong>200.00</strong>
                </p>
                <div className='product_rating'>
                    <p>⭐</p>
                    <p>⭐</p>
                    <p>⭐</p>
                </div>  
            </div>
            <img src='https://images-eu.ssl-images-amazon.com/images/G/31/in-certifiedrefurbished/gateway/Ver2_GW._SY304_CB446171261_.jpg' alt=''></img>
        </div>
    )
}

export default Product
